.menu {
  z-index: 2;
  width: 30px;
  height: 24px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.menu:before {
  content: "";
  width: 100%;
  height: 4px;
  background-color: #ff553e;
  border-radius: 4px;
  transition: transform .3s ease-in-out .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.menu:after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: #ff553e;
  border-radius: 4px;
  transition: transform .3s ease-in-out .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu.is-open {
  transform: rotate(-90deg);
}

.menu.is-open:before {
  transform: translateY(250%)rotate(45deg);
}

.menu.is-open:after {
  transform: translateY(-250%)rotate(-45deg);
}

.menu.is-open > .menu__item {
  transform: translateX(120%);
}

@media only screen and (min-width: 1200px) {
  .menu {
    display: none;
  }
}

.menu__item {
  width: 100%;
  height: 4px;
  background-color: #ff553e;
  border-radius: 4px;
  transition: transform .3s ease-in-out .3s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

