.stat {
  position: relative;

  overflow: hidden;
}

.stat__list {
  @extend %list;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 96px;

  @include bpoint(desktop) {
    flex-direction: row;
    gap: 180px;
  }
}

.stat__item {
  position: relative;

  text-align: center;

  &:not(:last-child)::after {
    content: '';
    position: absolute;
    bottom: -50%;
    left: 50%;

    transform: translate(-50%, 50%);

    display: block;
    width: 180px;
    height: 1px;
    background-color: getColor(primary);

    opacity: 0.6;

    @include bpoint(desktop) {
      display: none;
    }
  }
}

.stat__value {
  margin-bottom: 28px;

  font-family: getFont(secondary);
  font-weight: 600;
  font-size: 64px;
  line-height: 1;
  color: getColor(secondary);

  @include bpoint(desktop) {
    font-size: 48px;
    line-height: 1.33;
  }
}

.stat__text {
  font-size: 18px;
  line-height: 1;

  opacity: 0.6;
}

.stat__img {
  position: absolute;
  bottom: 0;
  left: -26px;

  width: 100px;
  height: 92px;
}
